/* Favorites page styles */
.empty-page {
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-top: 200px;
  max-width: 300px;
  color: var(--text-color);
  font-size: 14px;
  gap: 24px;
  margin-bottom: 60px;
}
.favorites-img-tablet,
.favorites-img-desktop {
  display: none;
}

.recipe-card {
  max-width: 258px;
  max-height: 287px;
}
.details-card-container {
  bottom: 16px;
  width: 80%;
}
.recipe-results {
  padding: 16px;
  gap: 60px;
}
/* Tablet */
@media screen and (min-width: 768px) {
  /* Favorites page styles */

  .empty-page {
    font-size: 16px;
    margin-top: 20px;
    max-width: 400px;
  }
  .favorites-img-tablet {
    display: block;
  }
  .recipe-card {
    max-width: 215px;
  }
  .recipe-results {
    max-width: 1100px;
    gap: 10px;
  }
}

/* Desktop */
@media screen and (min-width: 1280px) {
  /* Favorites page styles */
  .empty-page {
    max-width: 524px;
  }
  .favorites-img-desktop {
    display: block;
  }
  .favorites-img-tablet {
    display: none;
  }
  .recipe-card {
    max-width: 258px;
  }
}
